import './styles.css';
import menu from './menu.js';
import Handlebars from 'handlebars';
const input = document.getElementById('theme-switch-toggle');
const ulList = document.querySelector('.js-menu');
let body = document.querySelector('body');
const Theme = {
  LIGHT: 'light-theme',
  DARK: 'dark-theme',
};
input.addEventListener('change', () => {
  if (input.checked === false) {
    body.classList.remove(Theme.DARK);
    body.classList.add(Theme.LIGHT);
    localStorage.setItem('theme', Theme.LIGHT);
  } else {
    body.classList.remove(Theme.LIGHT);
    body.classList.add(Theme.DARK);
    localStorage.setItem('theme', Theme.DARK);
  }
});

if (localStorage.theme === Theme.LIGHT) {
  body.classList.add(Theme.LIGHT);
  input.checked = false;
} else if (localStorage.theme === Theme.DARK) {
  body.classList.add(Theme.DARK);
  input.checked = true;
}

let htmlTemplate = document.querySelector('.template').innerHTML;
const template = Handlebars.compile(htmlTemplate);
console.log(menu);
const wrapper = { objects: menu };
ulList.innerHTML = template(wrapper);
